import { MangaListType } from "../types/MangasTypes";

export const getTitlesByTags = (list: MangaListType, tags: string[]) => {
  const newList = {} as MangaListType;
  const listKeys = Object.keys(list);

  tags.forEach((tag) => {
    listKeys
      .filter(
        (title) =>
          !Object.keys(newList).includes(title) &&
          list[title].categories?.find((cat) => cat.name === tag)
      )
      .forEach((title) => {
        newList[title] = { ...list[title] };
      });
  });

  return newList
};

