import React from "react";
import i18n from "../i18n";
import { MangaListType } from "../types/MangasTypes";

export const handleUpdateTitleList = (fr: FileReader) => {
  const uploadList: MangaListType = JSON.parse(String(fr.result));
  const localList = getMangaList();
  const localListKeys = Object.keys(localList);
  Object.keys(uploadList).forEach((title) => {
    if (localListKeys.includes(title)) {
      if (
        parseFloat(String(localList[title].lastTime)) <
        parseFloat(String(uploadList[title].lastTime))
      ) {
        localList[title] = { ...localList[title], ...uploadList[title] };
      }
    } else {
      localList[title] = uploadList[title];
    }
  });
  localStorage.setItem("mangakeeper.toUpload", JSON.stringify(localList));
};

export const setToUpdate = (newValue: object) => {
  const toUpload = JSON.parse(
    localStorage.getItem("mangakeeper.toUpload") || "{}"
  );
  localStorage.setItem(
    "mangakeeper.toUpload",
    JSON.stringify({ ...toUpload, ...newValue })
  );
};

export const getLocalFile =
  (callback: (fr: FileReader, name?: string) => void) =>
  (evt: React.ChangeEvent<HTMLInputElement>) => {
    let tgt = evt.target;
    let files = tgt?.files || [];
    console.log("d");
    // FileReader support
    if (FileReader && files && files.length) {
      let fr = new FileReader();
      fr.onload = () => callback(fr, files[0]?.name);
      fr.readAsText(files[0]);
    }
  };

export function download(data: string, filename: string, type: string) {
  var file = new Blob([data], { type: type });
  if (window.navigator.msSaveOrOpenBlob)
    // IE10+
    window.navigator.msSaveOrOpenBlob(file, filename);
  else {
    // Others
    var a = document.createElement("a"),
      url = URL.createObjectURL(file);
    a.href = url;
    a.download = filename;
    document.body.appendChild(a);
    a.click();
    setTimeout(function () {
      document.body.removeChild(a);
      window.URL.revokeObjectURL(url);
    }, 0);
  }
}

export const getMangaList = (): MangaListType =>
  JSON.parse(localStorage.getItem("mangakeeper.mangaList") || "{}");

export const getLocalTags = () => {
  const mangaList: MangaListType = JSON.parse(
    localStorage.getItem("mangakeeper.mangaList") || "{}"
  );
  const tagTitles = Object.keys(mangaList).map((mangaName) =>
    mangaList[mangaName].categories?.map((t) => t.name)
  );
  return tagTitles.reduce((tagList, tagnamelist) => {
    const newTagList = { ...tagList };
    tagnamelist?.forEach((_t) => {
      const t = i18n(_t);
      newTagList[t] = (newTagList[t] || 0) + 1;
    });

    return newTagList;
  }, {} as { [tag: string]: number });
};
