import {Button} from "react-bootstrap";
import React, { useEffect, useMemo, useState } from "react";
import { Badge, Container, Form, ListGroup, Row } from "react-bootstrap";
import Select from "react-select";
import i18n from "../../i18n";
import SelectTags from "../../SelectTags";
import { ListType, MangaListType } from "../../types/MangasTypes";
import { getLocalTags, getMangaList } from "../../utils/file";
import { getTitlesByTags } from "../../utils/mangas";
import { parseTitle } from "../../utils/text";
import PopUp from "../PopUp";
import SideMenuList from "../SideMenuList";
import { FormContainer } from "./styles";

// import { Container } from './styles';

const ListPage: React.FC = () => {
  const [activeTab, setActiveTab] = useState("list");
  return (
    <Row style={{ overflow: "hidden" }}>
      <SideMenuList tab={activeTab} onChange={setActiveTab} />
      <ListTab show={activeTab === "list"} />
      <AddListTab show={activeTab === "add"} />
    </Row>
  );
};

const ListTab = ({ show }: { show: boolean }) => {
  const [mangaList, setMangaList] = useState(
    (getMangaList() || {}) as MangaListType
  );
  const [currentMangaList, setCurrentMangaList] = useState({} as MangaListType);

  const [lists, setLists] = useState(
    (mangaList["mangakeeper.lists"] || mocklist) as ListType[]
  );

  const [activeList, setActiveList] = useState(lists[0] || ({} as ListType));

  const handleRemoveManga = (mangaName: string) => {
    setMangaList((old) => {
      const newMangas = { ...old };
      if (mangaName) delete newMangas[mangaName];
      return newMangas;
    });
  };

  useEffect(() => {
    if (activeList?.name) {
      let newMangaList = {} as MangaListType;
      const mangaListKeys = Object.keys(mangaList);
      activeList.titles?.forEach((title) => {
        newMangaList[title] = { ...mangaList[title] };
      });

      if (activeList?.tags) {
        newMangaList = {
          ...newMangaList,
          ...getTitlesByTags(mangaList, activeList.tags),
        };
      }

      setCurrentMangaList(newMangaList);
    }
  }, [activeList]);

  return (
    <>
      {show && (
        <Container className="col-md-10 row" style={{ maxHeight: 550 }}>
          <ListGroup as="ol" numbered className="col-md-3">
            {lists.map((list) => {
              return (
                <ListGroup.Item
                  as="li"
                  className="d-flex justify-content-between align-items-start"
                  active={activeList.name === list.name}
                  key={list.name}
                  onClick={() => setActiveList(list)}
                >
                  <div className="ms-2 me-auto">
                    <div className="fw-bold">{list.name}</div>
                    {list.description}
                  </div>
                  <Badge variant="primary" pill>
                    {(list?.titles?.length || 0) +
                      (list?.tags?.reduce((sum, tag) => {
                        return [
                          ...sum,
                          ...Object.keys(mangaList)?.filter(
                            (title) =>
                              !list.titles?.includes(title) &&
                              !sum.includes(title) &&
                              mangaList[title].categories?.find(
                                (t) => t.name === tag
                              )
                          ),
                        ];
                      }, [] as string[])?.length || 0)}
                  </Badge>
                </ListGroup.Item>
              );
            })}
          </ListGroup>
          <PopUp
            mangas={currentMangaList}
            handleRemoveManga={handleRemoveManga}
            className="col-md-8"
          />
        </Container>
      )}
    </>
  );
};

type SelectOption = { value: string; label: string };

const AddListTab = ({ show }: { show: boolean }) => {
  const [allTags, setAllTags] = useState({});
  const [allTitles, setAllTitles] = useState(getMangaList());
  const [tags, setTags] = useState([] as SelectOption[]);
  const [titles, setTitles] = useState([] as SelectOption[]);
  const [excludeTitles, setExcludeTitles] = useState([] as SelectOption[]);
  const [description, setDescription] = useState("");
  const [nameList, setNameList] = useState("");

  const finalList = useMemo(() => {
    let _list: MangaListType = {
      ...getTitlesByTags(
        allTitles,
        tags.map((tag) => tag.value)
      ),
    };

    titles.forEach((title) => {
      _list[title.value] = allTitles[title.value];
    });
    excludeTitles.forEach((title) => {
      try {
        delete _list[title.value];
      } catch (error) {}
    });

    return _list;
  }, [allTitles, tags, titles, excludeTitles]);

  useEffect(() => {
    setAllTags(getLocalTags());
  }, []);

  const handleSubmit = ()=>{
    const listToSave:ListType = {
      name:nameList,
      description,
      tags:tags.map(tag=>tag.value),
      titles: titles.map(title=>title.value),
      exclude: excludeTitles.map(exclude=>exclude.value)
    }
  }
  return (
    <>
      {show && (
        <Row className="col-md-10">
          <FormContainer className="col-md-4">
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>{i18n("List name")}</Form.Label>
              <Form.Control
                type="text"
                value={nameList}
                onChange={(e) => setNameList(e.target.value)}
                placeholder={i18n("My Custom List")}
              />
            </Form.Group>
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1"
            >
              <Form.Label>{i18n("Description")}</Form.Label>
              <Form.Control
                value={description}
                onChange={(e) => setDescription(e.target.value)}
                as="textarea"
                rows={3}
              />
            </Form.Group>
            <Select
              isMulti
              value={tags}
              onChange={(e) => setTags(e)}
              options={Object.keys(allTags)
                .sort((a, b) => (a < b ? -1 : 1))
                .map((tag) => ({
                  value: tag,
                  label: `${tag} (${allTags[tag]})`,
                }))}
            />
            <Select
              isMulti
              value={titles}
              onChange={(e) => {
                setTitles(e);

                setExcludeTitles((prev) =>
                  prev.filter((title) =>
                    e.map((value) => !value.value).includes(title.value)
                  )
                );
              }}
              options={Object.keys(allTitles)
                .filter(
                  (title) =>
                    !Object.keys(
                      getTitlesByTags(
                        allTitles,
                        tags.map((tag) => tag.value)
                      )
                    ).includes(title)
                )
                .map((title) => ({
                  value: title,
                  label: parseTitle(title),
                }))}
            />
            <Select
              isMulti
              value={excludeTitles}
              onChange={(e) => {
                setExcludeTitles(e);

                setTitles((prev) =>
                  prev.filter((title) =>
                    e.map((value) => !value.value).includes(title.value)
                  )
                );
              }}
              options={Object.keys(allTitles).map((title) => ({
                value: title,
                label: parseTitle(title),
              }))}
            />
            <Button type="button" onClick={handleSubmit}>
              {i18n("Save")}
            </Button>
          </FormContainer>
          <PopUp className="col-md-6" mangas={finalList} />
        </Row>
      )}
    </>
  );
};

