import React, { useEffect, useState } from "react";
import { FloatingLabel, Form } from "react-bootstrap";
import { mocklist } from "../components/ListPage";
import i18n from "../i18n";
import { ListType, MangaListType } from "../types/MangasTypes";

// import { Container } from './styles';

const SelectTags: React.FC<{
  value: string;
  onChange: (value: string) => void;
  onChangeTags?: (value: string) => void;
  onChangeLists?: (value: string) => void;
  noTags?: boolean;
  noLists?: boolean;
  label?: string;
}> = ({
  value,
  onChange,
  onChangeTags,
  onChangeLists,
  label,
  noLists,
  noTags,
}) => {
  const [tags, setTags] = useState<{ [tag: string]: number }>({});
  const [lists, setLists] = useState([...mocklist] as ListType[]);

  useEffect(() => {
    const mangaList: MangaListType = JSON.parse(
      localStorage.getItem("mangakeeper.mangaList") || "{}"
    );
    const tagTitles = Object.keys(mangaList).map((mangaName) =>
      mangaList[mangaName].categories?.map((t) => t.name)
    );
    setTags(
      tagTitles.reduce((tagList, tagnamelist) => {
        const newTagList = { ...tagList };
        tagnamelist?.forEach((_t) => {
          const t = i18n(_t);
          newTagList[t] = (newTagList[t] || 0) + 1;
        });

        return newTagList;
      }, {} as { [tag: string]: number })
    );
  }, []);
  return (
    <FloatingLabel label={label} className="col-md-4">
      <Form.Select
        aria-label="Select Tags from your list"
        value={value}
        id="config-home-list"
        onChange={(e) => {
          const [type, value] = (e.target.value as string).split(".");

          if (value) {
            onChange(value);
            if (type === "tag") {
              onChangeTags?.(value);
            }

            if (type === "list") {
              onChangeLists?.(value);
            }
          }
        }}
      >
        <option>{i18n("Select the lists/tags")}</option>
        {!noLists && (
          <>
            <option disabled>-------{i18n("Lists")}--------</option>
            {lists
              .sort((a, b) => (a.name > b.name ? -1 : 1))
              .map((list) => (
                <option
                  key={`home-page-${list.name}`}
                  value={`list.${list.name}`}
                >{`${i18n(list.name)} (${list.name})`}</option>
              ))}
          </>
        )}
        {!noTags && (
          <>
            <option disabled>-------{i18n("Tags")}--------</option>
            {Object.keys(tags)
              .sort((a, b) => (tags[a] > tags[b] ? -1 : 1))
              .map((tag) => (
                <option key={`home-page-${tag}`} value={`tag.${tag}`}>{`${i18n(
                  tag
                )} (${tags[tag]})`}</option>
              ))}
          </>
        )}
      </Form.Select>
    </FloatingLabel>
  );
};

export default SelectTags;
