/* eslint-disable jsx-a11y/anchor-is-valid */

import React, { useMemo, useState } from "react";
import { ConfigType, MangaListType, MangaType } from "../../types/MangasTypes";
import Details from "../Details";
import {
  PopUpContainer,
  SliderContainer,
  SliderItemContainer,
  ThumbImg,
  TitleItem,
} from "./styles";

// import { Container } from './styles';
type Props = {
  mangas: MangaListType;
  handleRemoveManga: (mangaName: string) => void;
  className?: string;
};

type MangaFocusType = MangaType & { title?: string };
const PopUp: React.FC<Props> = ({ mangas, handleRemoveManga, className }) => {
  const [focus, setFocus] = useState({} as MangaFocusType);
  const [details, setDetails] = useState({} as MangaType);

  const hasDetails = useMemo(() => Object.keys(details).length > 0, [details]);
  const handleDetails = (info: MangaFocusType) => {
    setDetails(info);
  };
  const handleBackFromDetails = () => {
    setDetails({} as MangaFocusType);
  };

  return (
    <>
      {hasDetails ? (
        <Details
          className={className}
          onBack={handleBackFromDetails}
          info={details}
          handleRemoveManga={handleRemoveManga}
        />
      ) : (
        <PopUpContainer className={className}>
          <SliderContainer>
            {getMangas(mangas).map((mangaName, i) => {
              const manga = mangas[mangaName];
              return (
                <SliderItem
                  onMouseOver={() => setFocus({ ...manga, title: mangaName })}
                  onClick={() => handleDetails({ ...manga, title: mangaName })}
                  data={manga}
                  key={i.toString()}
                />
              );
            })}
          </SliderContainer>
        </PopUpContainer>
      )}
    </>
  );
};

const SliderItem: React.FC<{
  onMouseOver: () => void;
  onClick: () => void;
  data: MangaType;
  key?: string;
  label?: string;
}> = ({ onMouseOver, onClick, data, key, label }) => {
  return (
    <SliderItemContainer onMouseOver={onMouseOver} padding={8} key={key}>
      <a
        href={data.lastSource}
        onClick={!data.lastSource ? onClick : () => {}}
        target="blank"
      >
        <ThumbImg src={data.thumb} />
      </a>
      <TitleItem>
        <label onClick={onClick}>{label || "Details"}</label>
      </TitleItem>
    </SliderItemContainer>
  );
};

const getMangas = (mangas: MangaListType): string[] => {
  return Object.keys(mangas)
    .filter((title) =>
      !title.includes("mangakeeper.")
        ? parseConfigFilter(
            mangas[title] as MangaType,
            mangas["mangakeeper.configs"] as ConfigType
          )
        : false
    )
    .sort((a, b) =>
      (mangas[a]?.lastTime || 0) < (mangas[b].lastTime || 0) ? 1 : -1
    );
};

const parseConfigFilter = (manga: MangaType, configs: ConfigType): boolean => {
  let outFilter = true;
  if (!configs) return true;

  if (configs?.onlyread) {
    outFilter = outFilter && !!manga.lastCap;
  }

  if (configs?.homelist && configs?.homelist !== "null") {
    outFilter =
      outFilter &&
      !!manga.categories?.find((m) => m.name === configs?.homelist);
  }

  return outFilter;
};
export default PopUp;
